<template>
    <yd-layout>
        <div class="fdApproForm">
            <div class="top">
                <div class="header">
                    <div class="title">{{jsonData.requsername}}的问题反馈</div>
                    <div class="approveType" v-if="$route.params.status==='0'">待我处理</div>
                    <div class="approveType" v-if="$route.params.status==='1'">完成处理</div>
                    <i class="circle01"></i>
                    <i class="circle02"></i>
                </div>
                <div class="empInfo">
                    <div class="infoLeft infoTitle">问题类型：{{jsonData.approvalitemName}}</div>
                </div>
                <div v-if="jsonData.approvalitem==='a'">
                    <div class="empInfo" v-if="jsonData.reqtelA!=''">
                        <div class="infoLeft">手机号：</div>
                        <div class="infoRight">{{jsonData.reqtelA}}</div>
                    </div>
                    <div class="empInfo" v-if="jsonData.cardNumA!=''">
                        <div class="infoLeft">卡号：</div>
                        <div class="infoRight">{{jsonData.cardNumA}}</div>
                    </div>
                    <div class="empInfo" v-if="jsonData.description">
                        <div class="infoLeft">问题描述：</div>
                        <div class="infoRight desInfo" v-html="transferString(jsonData.description)"></div>
                    </div>
                </div>
                <div v-if="jsonData.approvalitem==='b'">
                    <div class="empInfo" v-if="jsonData.reqtelB!=''">
                        <div class="infoLeft">手机号：</div>
                        <div class="infoRight">{{jsonData.reqtelB}}</div>
                    </div>
                    <div class="empInfo" v-if="jsonData.cardnumB!=''">
                        <div class="infoLeft">卡号：</div>
                        <div class="infoRight">{{jsonData.cardnumB}}</div>
                    </div>
                    <div class="empInfo" v-if="jsonData.merchantCodeB!=''">
                        <div class="infoLeft">商户代码：</div>
                        <div class="infoRight">{{jsonData.merchantCodeB}}</div>
                    </div>
                    <div class="empInfo" v-if="jsonData.jioayiTimeB!=''">
                        <div class="infoLeft">交易时间：</div>
                        <div class="infoRight">{{jsonData.jioayiTimeB}}</div>
                    </div>
                    <div class="empInfo" v-if="jsonData.jiaoyiMoneyB!=''">
                        <div class="infoLeft">交易金额：</div>
                        <div class="infoRight">{{jsonData.jiaoyiMoneyB}}</div>
                    </div>
                    <div class="empInfo" v-if="jsonData.description">
                        <div class="infoLeft">问题描述：</div>
                        <div class="infoRight desInfo" v-html="transferString(jsonData.description)"></div>
                    </div>
                </div>
                <div v-if="jsonData.approvalitem==='c'">
                    <div class="empInfo" v-if="jsonData.activityidC!=''">
                        <div class="infoLeft">活动ID：</div>
                        <div class="infoRight">{{jsonData.activityidC}}</div>
                    </div>
                    <div class="empInfo" v-if="jsonData.merchantNameC!=''">
                        <div class="infoLeft">商户名称：</div>
                        <div class="infoRight">{{jsonData.merchantNameC}}</div>
                    </div>
                    <div class="empInfo" v-if="jsonData.shopNameC!=''">
                        <div class="infoLeft">门店名称：</div>
                        <div class="infoRight">{{jsonData.shopNameC}}</div>
                    </div>
                    <div class="empInfo" v-if="jsonData.jioayiCardC!=''">
                        <div class="infoLeft">交易卡号：</div>
                        <div class="infoRight">{{jsonData.jioayiCardC}}</div>
                    </div>
                    <div class="empInfo" v-if="jsonData.jiaoyiMoneyC!=''">
                        <div class="infoLeft">交易金额：</div>
                        <div class="infoRight">{{jsonData.jiaoyiMoneyC}}</div>
                    </div>
                    <div class="empInfo" v-if="jsonData.jiaoyiTimeC!=''">
                        <div class="infoLeft">交易时间：</div>
                        <div class="infoRight">{{jsonData.jiaoyiTimeC}}</div>
                    </div>
                    <div class="empInfo" v-if="jsonData.reqtelC!=''">
                        <div class="infoLeft">手机号：</div>
                        <div class="infoRight">{{jsonData.reqtelC}}</div>
                    </div>
                    <div class="empInfo" v-if="jsonData.merchantCodeC!=''">
                        <div class="infoLeft">商户代码：</div>
                        <div class="infoRight">{{jsonData.merchantCodeC}}</div>
                    </div>
                    <div class="empInfo" v-if="jsonData.description!=''">
                        <div class="infoLeft">问题描述：</div>
                        <div class="infoRight desInfo" v-html="transferString(jsonData.description)"></div>
                    </div>
                </div>
            </div>
            <div class="bottom" v-if="jsonData.handleResult!=''||jsonData.backreasonChuli!=''">
                <div class="botTitle">
                    处理结果反馈
                </div>
                <div class="record" v-if="jsonData.handleResult!=''">
                    <div class="nameSugg">
                        <div class="name">{{jsonData.handleResultPerson}}：</div>
                        <div class="suggest">{{jsonData.handleResult}}</div>
                    </div>
                    <div class="recorddate">{{jsonData.handleResultTime}}</div>
                </div>
                <div class="record" v-if="jsonData.backreasonChuli!=''">
                    <div class="nameSugg">
                        <div class="name">{{jsonData.backreasonChuliPerson}}：</div>
                        <div class="suggest">{{jsonData.backreasonChuli}}</div>
                    </div>
                    <div class="recorddate">{{jsonData.backreasonChuliTime}}</div>
                </div>
                <!-- <div class="record" v-if="jsonData.backreasonTijiao!=''">
                    <div class="nameSugg">
                        <div class="name">{{jsonData.chuliName}}：</div>
                        <div class="suggest">{{jsonData.backreasonTijiao}}</div>
                    </div>
                    <div class="recorddate">{{jsonData.reqstartdate}}</div>
                </div> -->
            </div>
        </div>
        <yd-popup v-model="show" position="center" width="5.79rem">
            <div class="other">退回原因</div>
            <yd-cell-group class="otherInput">
                <yd-cell-item>
                    <yd-textarea slot="right" v-model="opinion" placeholder="请您输入退回原因..."></yd-textarea>
                </yd-cell-item>
            </yd-cell-group>
            <div class="otherBtn">
                <ul>
                    <li class="no" @click="show=false">取消</li>
                    <li class="yes" @click="returnApply()">确认</li>
                </ul>
            </div>
        </yd-popup>
        <yd-popup v-model="returnShow" position="center" width="5.79rem">
            <div class="other">问题反馈内容</div>
            <yd-cell-group class="otherInput">
                <yd-cell-item>
                    <yd-textarea slot="right" v-model="opinion" placeholder="请您输入问题反馈内容..."></yd-textarea>
                </yd-cell-item>
            </yd-cell-group>
            <div class="otherBtn">
                <ul>
                    <li class="no" @click="returnShow=false">取消</li>
                    <li class="yes" @click="resend()">确认</li>
                </ul>
            </div>
        </yd-popup>
        <yd-tabbar slot="tabbar" v-if="$route.params.page==1&&$route.params.status==0">
            <div class="agree" @click="$router.push('/fdAgent/'+$route.params.workId+'/'+comfiLeaderId)">受理</div>
            <div class="back" @click="show=true">退回</div>
        </yd-tabbar>
        <yd-tabbar slot="tabbar" v-if="$route.params.page==2&&$route.params.status==0">
            <div class="agree" @click="finish()">确认结束</div>
            <div class="back" @click="returnShow=true">再次发送</div>
        </yd-tabbar>
    </yd-layout>
</template>
<script>
import { mapGetters, mapMutations } from 'vuex';
export default {
    name: 'fdApproForm',
    data() {
        return {
            jsonData: {},
            leaderIDs: '',
            show: false,
            returnShow: false,
            opinion: '',
            comfiLeaderId: ''
        }
    },
    mounted() {
        let vm = this;
        document.setTitle('营销问题反馈');
        vm.queryForm();
    },
    computed: {
        ...mapGetters([
            'getUserInfo'
        ])
    },
    methods: {
        ...mapMutations([
            'setFeedbackInfo'
        ]),
        queryForm() {
            let vm = this;
            vm.get(vm.conf.portalMobile + '/API/WorkFlow/oawf/queryForm', {
                'type': 'troubleshootingMain',
                'workItemID': vm.$route.params.workId,
                'token': `${vm.getUserInfo.ssotoken}L/wp-2q${vm.getUserInfo.ssotokenCode}`
            }, function(res) {
                if (res.jsonData.CURRENTSTATE != 10 && res.jsonData.CURRENTSTATE != 4 && vm.$route.params.status == 0 && res.isSuccess == 'true') {
                    vm.$dialog.toast({
                        mes: '该流程已被处理',
                        timeout: 2000,
                        callback: () => {
                            setTimeout(function() {
                                if (vm.$route.params.status == 0) {
                                    vm.$router.push('/handle');
                                }
                            })
                        }
                    });
                    if (vm.$route.params.status == 0) {
                        return false;
                    }
                }
                if (res.isSuccess === 'true') {
                    vm.jsonData = res.jsonData;
                    vm.setFeedbackInfo({
                        jsonData: vm.jsonData
                    });
                    for (let item in vm.jsonData.nextBtn) {
                        if (item === 'submitResultActivity') {
                            vm.leaderIDs = vm.jsonData.nextBtn.submitResultActivity[0];
                            console.log(vm.leaderIDs);
                        } else if (item === 'resultConfirmActivity') {
                            vm.comfiLeaderId = vm.jsonData.nextBtn.resultConfirmActivity[0]
                        } else if (item === 'handleResultActivity') {
                            vm.leaderIDs = vm.jsonData.nextBtn.handleResultActivity[0];
                        }
                    }
                }
            })
        },
        resend() {
            let vm = this;
            if (vm.opinion == '') {
                vm.$dialog.toast({
                    mes: '请您输入问题反馈内容',
                    timeout: 2000
                });
            } else {
                vm.get(vm.conf.portalMobile + '/API/WorkFlow/oawf/doOANext', {
                    'type': 'troubleshootingMain',
                    'workItemID': vm.$route.params.workId,
                    'personID': vm.getUserInfo.userid,
                    'activityID': 'handleResultActivity',
                    'token': `${vm.getUserInfo.ssotoken}L/wp-2q${vm.getUserInfo.ssotokenCode}`,
                    'leaderIDs': vm.leaderIDs,
                    'opinion': vm.opinion,
                    'workTitle': vm.jsonData.reqdeptname + vm.jsonData.requsername + '营销问题反馈',
                    'ext': ''
                }, function(res) {
                    if (res.isSuccess == 'true') {
                        vm.$dialog.toast({
                            mes: '发送成功',
                            timeout: 2000,
                            callback: () => {
                                vm.$router.push('/handle');
                            }
                        });
                    }
                })
            }
        },
        finish() {
            let vm = this;
            vm.get(vm.conf.portalMobile + '/API/WorkFlow/oawf/doOANext', {
                'type': 'troubleshootingMain',
                'workItemID': vm.$route.params.workId,
                'personID': vm.getUserInfo.userid,
                'activityID': 'finishActivity',
                'token': `${vm.getUserInfo.ssotoken}L/wp-2q${vm.getUserInfo.ssotokenCode}`,
                'leaderIDs': '',
                'workTitle': vm.jsonData.reqdeptname + vm.jsonData.requsername + '营销问题反馈',
                'opinion': ''
            }, function(res) {
                if (res.isSuccess == 'true') {
                    vm.$dialog.toast({
                        mes: '发送成功',
                        timeout: 2000,
                        callback: () => {
                            vm.$router.push('/handle');
                        }
                    });
                }
            })
        },
        returnApply() {
            let vm = this;
            if (vm.opinion == '') {
                vm.$dialog.toast({
                    mes: '请您输入退回原因',
                    timeout: 2000
                });
            } else {
                vm.get(vm.conf.portalMobile + '/API/WorkFlow/oawf/doOANext', {
                    'type': 'troubleshootingMain',
                    'workItemID': vm.$route.params.workId,
                    'personID': vm.getUserInfo.userid,
                    'activityID': 'submitResultActivity',
                    'token': `${vm.getUserInfo.ssotoken}L/wp-2q${vm.getUserInfo.ssotokenCode}`,
                    'leaderIDs': vm.leaderIDs,
                    'workTitle': vm.jsonData.reqdeptname + vm.jsonData.requsername + '营销问题反馈',
                    'opinion': vm.opinion
                }, function(res) {
                    if (res.isSuccess == 'true') {
                        vm.$dialog.toast({
                            mes: '发送成功',
                            timeout: 2000,
                            callback: () => {
                                vm.$router.push('/handle');
                            }
                        });
                    }
                })
            }
        }
    }
}
</script>
<style lang="less" scoped>
@import 'feedback.less';

// 底部按钮
.agree,
.back {
    flex: 1;
    color: #fff;
    font-size: .36rem;
    line-height: 1rem;
    text-align: center;
}

.agree {
    background-color: #59a5fd;
}

.back {
    background-color: #8dce3f;
}

.circle01 {
    height: .2rem;
    width: .2rem;
    background-color: #f1f1f1;
    position: absolute;
    bottom: -0.1rem;
    left: -0.1rem;
    border-radius: 50%;
    z-index: 2
}

.circle02 {
    height: .2rem;
    width: .2rem;
    background-color: #f1f1f1;
    position: absolute;
    bottom: -0.1rem;
    right: -0.1rem;
    border-radius: 50%;
    z-index: 2
}

.infoTitle {
    font-weight: bold;
    color: #333;
}
.desInfo {
    -webkit-box-flex: 1;
    -webkit-flex: 1;
    -ms-flex: 1;
    flex: 1;
    line-height: .5rem;
    padding-top: .25rem;
    padding-bottom: .2rem;
}
</style>


